/* 3D Tech Stack Animations */
.tech-stack-3d-container {
  position: relative;
  overflow: hidden;
  padding: 40px 0;
}

.tech-3d-col {
  padding: 20px 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tech-3d-tilt {
  width: 120px;
  height: 120px;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.tech-3d-icon {
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: all 0.3s ease;
  -webkit-animation: float 6s ease-in-out infinite;
          animation: float 6s ease-in-out infinite;
  -webkit-animation-delay: var(--float-delay);
          animation-delay: var(--float-delay);
}

.tech-3d-inner {
  position: relative;
  width: 100%;
  height: 100%;
  background: linear-gradient(145deg, 
    rgba(255, 255, 255, 0.1), 
    rgba(255, 255, 255, 0.05)
  );
  border-radius: 20px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    0 0 20px rgba(var(--tech-color), 0.3);
}

.tech-3d-icon-wrapper {
  position: relative;
  z-index: 2;
  -webkit-transform: translateZ(20px);
          transform: translateZ(20px);
}

.tech-icon {
  font-size: 3rem;
  color: var(--tech-color);
  -webkit-filter: drop-shadow(0 0 10px rgba(var(--tech-color), 0.5));
          filter: drop-shadow(0 0 10px rgba(var(--tech-color), 0.5));
  transition: all 0.3s ease;
}

.tech-3d-glow {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 80px;
  height: 80px;
  background: radial-gradient(circle, var(--tech-color) 0%, transparent 70%);
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%) translateZ(-10px);
          transform: translate(-50%, -50%) translateZ(-10px);
  opacity: 0.3;
  -webkit-animation: pulse 2s ease-in-out infinite;
          animation: pulse 2s ease-in-out infinite;
}

.tech-3d-reflection {
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  height: 40%;
  background: linear-gradient(
    135deg,
    rgba(255, 255, 255, 0.4) 0%,
    transparent 50%
  );
  border-radius: 15px;
  -webkit-transform: translateZ(25px);
          transform: translateZ(25px);
  opacity: 0.6;
}

.tech-3d-label {
  position: absolute;
  bottom: -35px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 0.9rem;
  font-weight: 600;
  color: var(--tech-color);
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: all 0.3s ease;
}

.tech-3d-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
}

.particle {
  position: absolute;
  width: 3px;
  height: 3px;
  background: var(--tech-color);
  border-radius: 50%;
  opacity: 0;
  -webkit-animation: particleFloat 4s ease-in-out infinite;
          animation: particleFloat 4s ease-in-out infinite;
}

.particle-0 { top: 10%; left: 10%; -webkit-animation-delay: 0s; animation-delay: 0s; }
.particle-1 { top: 20%; right: 10%; -webkit-animation-delay: 0.5s; animation-delay: 0.5s; }
.particle-2 { bottom: 20%; left: 20%; -webkit-animation-delay: 1s; animation-delay: 1s; }
.particle-3 { bottom: 10%; right: 20%; -webkit-animation-delay: 1.5s; animation-delay: 1.5s; }
.particle-4 { top: 50%; left: 5%; -webkit-animation-delay: 2s; animation-delay: 2s; }
.particle-5 { top: 30%; right: 5%; -webkit-animation-delay: 2.5s; animation-delay: 2.5s; }

/* Hover Effects */
.tech-3d-icon.hovered {
  -webkit-transform: translateY(-10px) rotateX(15deg);
          transform: translateY(-10px) rotateX(15deg);
}

.tech-3d-icon.hovered .tech-icon {
  -webkit-transform: scale(1.2) rotateY(360deg);
          transform: scale(1.2) rotateY(360deg);
  color: #fff;
  -webkit-filter: drop-shadow(0 0 20px var(--tech-color));
          filter: drop-shadow(0 0 20px var(--tech-color));
}

.tech-3d-icon.hovered .tech-3d-glow {
  opacity: 0.8;
  -webkit-transform: translate(-50%, -50%) translateZ(-10px) scale(1.5);
          transform: translate(-50%, -50%) translateZ(-10px) scale(1.5);
}

.tech-3d-icon.hovered .tech-3d-label {
  opacity: 1;
  -webkit-transform: translateX(-50%) translateY(-5px);
          transform: translateX(-50%) translateY(-5px);
}

.tech-3d-icon.hovered .particle {
  opacity: 1;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
}

/* Keyframe Animations */
@-webkit-keyframes float {
  0%, 100% { -webkit-transform: translateY(0px) rotateX(0deg); transform: translateY(0px) rotateX(0deg); }
  33% { -webkit-transform: translateY(-10px) rotateX(5deg); transform: translateY(-10px) rotateX(5deg); }
  66% { -webkit-transform: translateY(-5px) rotateX(-5deg); transform: translateY(-5px) rotateX(-5deg); }
}
@keyframes float {
  0%, 100% { -webkit-transform: translateY(0px) rotateX(0deg); transform: translateY(0px) rotateX(0deg); }
  33% { -webkit-transform: translateY(-10px) rotateX(5deg); transform: translateY(-10px) rotateX(5deg); }
  66% { -webkit-transform: translateY(-5px) rotateX(-5deg); transform: translateY(-5px) rotateX(-5deg); }
}

@-webkit-keyframes pulse {
  0%, 100% { opacity: 0.3; -webkit-transform: translate(-50%, -50%) translateZ(-10px) scale(1); transform: translate(-50%, -50%) translateZ(-10px) scale(1); }
  50% { opacity: 0.6; -webkit-transform: translate(-50%, -50%) translateZ(-10px) scale(1.1); transform: translate(-50%, -50%) translateZ(-10px) scale(1.1); }
}

@keyframes pulse {
  0%, 100% { opacity: 0.3; -webkit-transform: translate(-50%, -50%) translateZ(-10px) scale(1); transform: translate(-50%, -50%) translateZ(-10px) scale(1); }
  50% { opacity: 0.6; -webkit-transform: translate(-50%, -50%) translateZ(-10px) scale(1.1); transform: translate(-50%, -50%) translateZ(-10px) scale(1.1); }
}

@-webkit-keyframes particleFloat {
  0% { 
    opacity: 0; 
    -webkit-transform: translateY(0px) scale(0); 
            transform: translateY(0px) scale(0); 
  }
  50% { 
    opacity: 1; 
    -webkit-transform: translateY(-20px) scale(1); 
            transform: translateY(-20px) scale(1); 
  }
  100% { 
    opacity: 0; 
    -webkit-transform: translateY(-40px) scale(0); 
            transform: translateY(-40px) scale(0); 
  }
}

@keyframes particleFloat {
  0% { 
    opacity: 0; 
    -webkit-transform: translateY(0px) scale(0); 
            transform: translateY(0px) scale(0); 
  }
  50% { 
    opacity: 1; 
    -webkit-transform: translateY(-20px) scale(1); 
            transform: translateY(-20px) scale(1); 
  }
  100% { 
    opacity: 0; 
    -webkit-transform: translateY(-40px) scale(0); 
            transform: translateY(-40px) scale(0); 
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .tech-3d-tilt {
    width: 90px;
    height: 90px;
  }
  
  .tech-icon {
    font-size: 2.5rem;
  }
  
  .tech-3d-label {
    font-size: 0.8rem;
    bottom: -30px;
  }
}
